<script>
// import { getColor } from "../../common/utils"
function getColor(opacity) {
  const r = parseInt(Math.random() * 255);
  const g = parseInt(Math.random() * 255);
  const b = parseInt(Math.random() * 255);
  const a = Math.random();
  return `rgba(${r},${g},${b},${opacity ? opacity : a})`;
}

function msin(v) {
  return Math.sin(v);
}

function mcos(v) {
  return Math.cos(v);
}

export default {
  data() {
    return {
      k: 1 / 3,
      list: [
        {
          id: 1,

          text: "å¡å£ç¹ä½",
        },

        {
          id: 2,

          text: "äººè¸ç¹ä½",
        },

        {
          id: 3,

          text: "é«ç©ºç¹ä½",
        },

        {
          id: 4,

          text: "è§é¢ç¹ä½",
        },

        {
          id: 5,

          text: "ä¼æ¯ä¼æ¯",
        },

        {
          id: 6,

          text: "yyyy",
        },

        {
          id: 7,

          text: "oooo",
        },

        {
          id: 8,

          text: "kkkk",
        },

        {
          id: 9,

          text: "yyyy",
        },

        {
          id: 10,

          text: "oooo",
        },

        {
          id: 11,

          text: "kkkk",
        },
        {
          id: 12,

          text: "kkkk",
        },
      ],
    };
  },

  computed: {
    listStyle() {
      return {
        width: 2 * this.outerR,
        height: 2 * this.outerR,
      };
    },

    /* å¼§åº¦å¶çå¼ */
    itemArc() {
      return (Math.PI * 2) / this.list.length;
    },
    /* è§åº¦å¼çå¼ */
    itemDeg() {
      return 360 / this.list.length;
    },
    /* ç¯æ®µåºåçé«åº¦å å¤§åå½¢åå¾çæ¯å¼ */
    conHeiK() {
      return 1 - this.k;
    },
    /* ç¯æ®µåºåå°ºå¯¸åä½ç½®*/
    conStyle() {
      const Î¸ = this.itemArc;
      const Î± = this.itemDeg;
      const k = this.conHeiK;
      const f = this.format;
      let conWidth = f(2 * msin(Î¸ / 2));
      const conHeight = f((1 / msin(Î¸)) * k);
      const moveY = f(1 - 1 / k + mcos(Î¸ / 2) / k);
      return {
        width: conWidth + "%",
        height: conHeight + "%",
        transform: `skew(-${90 - Î±}deg) rotate(-${
          90 - Î± / 2
        }deg) translateY(${moveY}%)`,
      };
    },

    /* æå½¢çå°ºå¯¸åå®ä½ */
    wrapStyle() {
      const wrapHeight = this.format(msin(this.itemArc) / 2) + "%";
      const wrapTop = 50 - parseInt(wrapHeight) + "%";
      return { width: "50%", height: wrapHeight, top: wrapTop, left: 0 };
    },
  },

  methods: {
    format(v) {
      return (v * 100).toFixed(2);
    },

    getStyle(i) {
      const { width, height, left, top } = this.wrapStyle;
      const Î¸ = this.itemDeg;
      return {
        transform: `rotateZ(${i * Î¸}deg)  skew(${90 - Î¸}deg)`, //rotate è¦å¨ skew åé¢
        backgroundColor: getColor(1),
      };
    },

    handleClickedItem(e) {
      console.log(e.target);
    },
  },
};
</script>

