<template>
  <main class="three-demo" ref="rdrWrapper">
    <canvas id="rederer-dom" ref="rdrDom"></canvas>
  </main>
</template>

<scriptÂ src="https://wow.techbrood.com/libs/three.r73.js"></script>

<script>
import BaseThree from "../components/BasicThree";
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
import { Line2 } from "three/examples/jsm/lines/Line2.js";
import { LineMaterial } from "three/examples/jsm/lines/LineMaterial.js";
import { LineGeometry } from "three/examples/jsm/lines/LineGeometry.js";
import { GeometryUtils } from "three/examples/jsm/utils/GeometryUtils.js";
import { randomRangeInt } from "../common/utils";

export default {
  mixins: [BaseThree],

  created() {},

  mounted() {
    this.init();
    this.initBroswerEvent();
    this.initR();
    // this.createPlaneGroup();
    //Â this.createCubeGroup()
    // this.createStaticBasicCircle();
    this.createText();
    this.createBottomLines();
    this.createGather();
    // this.drawUpArrow();
    // this.drawCapital();
    this.drawTopCircles();
    // this.drawCapitalBase();
  },

  data() {
    this.GREEN_LINE_COLOR = "#00ff77";

    this.WHITE_LINE_COLOR = "#ffffff";

    this.GREEN_LINE_WIDTH = 1;

    this.YELLOW_POINT_COLOR = "#ffff00";

    return {
      itemNum: 17,

      topR: 50,

      bottomR: 50
    };
  },

  methods: {
    initR() {
      this.topR = this.getR(48, 0, this.itemNum); //Â åå°é¡¶åå¾
      this.bottomR = this.getR(55, 0, this.itemNum); //Â åå°åºåå¾
    }, //Â å¸å±å¨å´çç½çº¿å

    drawTopCircles() {
      const lr = this.bottomR;

      const rs = [
        lr,
        0.22 * lr,
        0.22 * lr + 1,
        0.3 * lr,
        0.2 * lr,
        ,
        0.1 * lr + 1,
        0.1 * lr
      ]; //Â çº¿ååå¾
      const dss = [1, 1, 0, 1, 0, 0, 1, 0]; //Â çº¿åÂ dashsizeÂ ä»æé«å°æä½ç
      const gss = [1, 1, 0, 1, 0, 0, 1, 0]; //Â çº¿åé´éå¤§å°Â ä»æé«å°æä½ç
      const py = [90, 80, 80, 50, 40, 30, 28, 20]; //Â çº¿åå¨Â yÂ è½´ä½ç½®Â ä»æé«å°æä½ç
      const group = new THREE.Group();

      for (let i = 0; i < rs.length; i++) {
        const line = this.createCircleDashLine(
          rs[i],
          this.GREEN_LINE_WIDTH,
          dss[i],
          gss[i],
          this.WHITE_LINE_COLOR
        );

        line.position.set(0, py[i], 0);

        group.add(line);
      }

      this.scene.add(group);
    }, //Â å¸å±ç

    drawCapital() {
      const w = this.topR * 0.7;

      const spriteMap = this.textureLoader.load(
        `textures/three-js-demo/capital.png`
      );

      const spriteMaterial = new THREE.SpriteMaterial({
        map: spriteMap,
        depthTest: false
      });

      const sprite = new THREE.Sprite(spriteMaterial);

      sprite.position.set(0, 120, -5);

      sprite.scale.set(w, w, 1);

      this.scene.add(sprite);
    }, //Â å¸å±åºé¢

    drawCapitalBase() {
      const r = this.topR * 0.23;

      const h = 80;

      const geometry = new THREE.CircleBufferGeometry(r, 100);

      const texture = this.textureLoader.load(
        `textures/three-js-demo/capital-bottom.png`
      );

      const material = new THREE.MeshBasicMaterial({
        map: texture,

        transparent: true
      });

      const circle = new THREE.Mesh(geometry, material);

      circle.position.set(0, h, 0);

      circle.rotateOnAxis(this.XAV, -Math.PI / 2);

      this.scene.add(circle);
    }, //Â ä¸é¨åç®­å¤´

    drawUpArrow() {
      const r = this.topR * 0.5;

      const spriteMap = this.textureLoader.load(
        `textures/three-js-demo/up-arrow.png`
      );

      const spriteMaterial = new THREE.SpriteMaterial({
        map: spriteMap,
        depthTest: false
      });

      const sprite = new THREE.Sprite(spriteMaterial);

      sprite.position.set(0, 20, -5);

      sprite.scale.set(r, r, 1);

      this.scene.add(sprite);
    }, //Â æ±èçº¿

    createGather() {
      const lineN = 50; //Â çº¿çä¸ªæ°
      const tps = this.getPosition(this.topR * 0.15, lineN); //Â æ±èç»ç¹åæ (x,z)
      const bps = this.getPosition(this.topR * 0.9, lineN); //Â æ±èèµ·ç¹åæ (x,z)
      const endY = 70; //èç»ç¹yè½´åæ 
      const startY = 10; //èµ·ç¹yè½´åæ 
      const group = new THREE.Group();

      for (let i = 0; i < lineN; i++) {
        const start = bps[i];

        const end = tps[randomRangeInt(0, lineN)];

        const c1 = [
          ((start[0] + end[0]) * 4) / 5,
          ((startY + endY) * 3) / 5,
          ((start[1] + end[1]) * 4) / 5
        ];

        const c2 = [
          ((start[0] + end[0]) * 1) / 5,
          ((startY + endY) * 2) / 5,
          ((start[1] + end[1]) * 1) / 5
        ];

        const curve = new THREE.CubicBezierCurve3(
          new THREE.Vector3(start[0], startY, start[1]),
          new THREE.Vector3(...c1),
          new THREE.Vector3(...c2),
          new THREE.Vector3(end[0], endY, end[1])
        );

        const points = curve.getPoints(endY - startY);

        const line = this.createRunningLight(points);

        const point = this.createRunningPoint();

        point.position.set(start[0], startY, start[1]);

        this.setPointAnimation(point, points);

        line.add(point);

        group.add(line);
      }

      this.scene.add(group);
    },

    createRunningLight(points) {
      const geometry = new THREE.BufferGeometry().setFromPoints(points);

      const material = new THREE.LineDashedMaterial({
        color: this.GREEN_LINE_COLOR,
        linewidth: 1.5,
        dashSize: 1,
        gapSize: 0.1
      });

      const line = new THREE.Line(geometry, material);

      line.computeLineDistances();

      return line;
    }, //æ±èçº¿ä¸çç¹

    createRunningPoint() {
      const geometry = new THREE.SphereBufferGeometry(0.8);

      const material = new THREE.MeshBasicMaterial({
        color: this.YELLOW_POINT_COLOR
      });

      const sphere = new THREE.Mesh(geometry, material);

      return sphere;
    }, //Â æ±èçº¿ä¸çç¹çå¨ç»

    setPointAnimation(point, xyzs) {
      const timeSlice = xyzs.map((item, index) => index);

      const flatxyz = xyzs.reduce((acc, item, i) => {
        return acc.concat(item.x, item.y, item.z);
      }, []);

      const positionKF = new THREE.VectorKeyframeTrack(
        ".position",
        timeSlice,
        flatxyz
      );

      

      const animation = new THREE.AnimationClip("Action", 5, [positionKF]);

      const mixer = new THREE.AnimationMixer(point);

      this.mixers.push(mixer);

      const clipAction = mixer.clipAction(animation);
      
      clipAction.play();
    }, //ä¸­é´é¨åéæåºåº§

    createStaticBasicCircle() {
      const group = new THREE.Group();

      const sr = this.topR;

      const lr = this.bottomR;

      const rArr = [lr + 1, 0.6 * sr, sr + (lr - sr) / 2, sr, sr - 1]; //Â éæåºåº§åå¾
      const yArr = [0, 0.1, 10, 20, 19];

      for (let i = 0; i < rArr.length; i++) {
        const geometry = new THREE.CircleBufferGeometry(rArr[i], 100);

        const texture = this.textureLoader.load(
          `textures/three-js-demo/base${i}.png`
        );

        const material = new THREE.MeshBasicMaterial({
          map: texture,

          transparent: true
        });

        const circle = new THREE.Mesh(geometry, material);

        circle.position.set(0, yArr[i], 0);

        circle.rotateOnAxis(this.XAV, -Math.PI / 2);

        group.add(circle);
      }

      this.scene.add(group);
    }, //Â ä¸­é´åºåçå¼§å½¢å¹³é¢

    createPlaneGroup() {
      const num = this.itemNum; //Â åå²æ°é
      const h = 23; //Â åå°é«
      const group = new THREE.Group();

      for (let i = 0; i < num; i++) {
        const itemA = (2 * Math.PI) / num;

        const geometry = new THREE.CylinderBufferGeometry(
          this.topR,
          this.bottomR,
          h,
          num,
          1,
          true,
          itemA * i,
          itemA
        );

        const texture = this.textureLoader.load(
          `textures/three-js-demo/arc-surface.png`
        );

        const material = new THREE.MeshBasicMaterial({
          map: texture,

          transparent: true
        });

        const cylinder = new THREE.Mesh(geometry, material);

        group.add(cylinder);
      }

      group.position.set(0, h / 2, 0);

      this.scene.add(group);
    }, //Â ä¸­é´é¨ååºåé¨å(ä¸¤ä¸ªæ­£æ¹ä½)

    createCubeGroup() {
      const lr = this.bottomR;

      const position = this.getPosition(0.8 * lr, this.itemNum, -Math.PI / 7);

      const group = new THREE.Group();

      for (let i = 0; i < this.itemNum; i++) {
        const spriteMap = this.textureLoader.load(
          `textures/three-js-demo/cube.png`
        );

        const spriteMaterial = new THREE.SpriteMaterial({
          map: spriteMap,
          depthTest: false
        });

        const sprite = new THREE.Sprite(spriteMaterial);

        sprite.position.set(position[i][0], 10, position[i][1]);

        sprite.scale.set(26, 26, 1);

        group.add(sprite);
      }

      this.scene.add(group);
    }, //Â ä¸­é´é¨åæå­

    createText() {
      this.fontLoader.load(
        this.fontUrl,

        font => {
          const num = this.itemNum;

          const h = 13; //åå¸å¨Â yÂ è½´çé«åº¦
          const iA2 = Math.PI / 2.3; //Â ä¸å¼§å½¢çå¹³è¡Â æ²¿çyè½´èªè½¬åå§è§åº¦
          const iA3 = -Math.PI / 18; //Â ä¸å¼§å½¢çå¹³è¡Â æ²¿çxè½´èªè½¬è§åº¦
          const rA = Math.PI / 180; //Â ä¸å¼§å½¢çå·¦å³å¯¹é½Â groupÂ éè¦èªè½¬çè§åº¦
          const group = new THREE.Group();

          const sr = this.topR;

          const position = this.getPosition(
            this.bottomR + 0.1,
            num,
            Math.PI / 10
          );

          for (let i = 0; i < num; i++) {
            const itemA = (2 * Math.PI) / num;

            const matLite = new THREE.MeshBasicMaterial({
              color: new THREE.Color(0xffffff)
            });

            const geometry = new THREE.TextGeometry(`${i < 10 ? "0" + i : i}`, {
              font: font,

              size: 7,

              height: 1,

              curveSegments: 10
            });

            const labelName = new THREE.Mesh(geometry, matLite);

            labelName.position.set(position[i][0], h, position[i][1]);

            labelName.rotateOnAxis(this.YAV, iA2 - i * itemA);

            labelName.rotateOnAxis(this.XAV, iA3);

            group.add(labelName); //Â group.rotateOnAxis(this.YAV,Â rA)
          }

          this.scene.add(group);
        },

        function(xhr) {
          console.log((xhr.loaded / xhr.total) * 100 + "%Â loaded");
        },

        function(err) {
          console.log("AnÂ errorÂ happened");
        }
      );
    }, //Â ä¸é¢é¨åèçº¿å

    createBottomLines() {
      const lr = this.bottomR;

      const rs = [lr, 1.2 * lr, 1.2 * lr + 3, 1.5 * lr, 1.5 * lr + 3]; //Â çº¿ååå¾
      const dss = [6, 1, 1, 0, 2]; //Â çº¿åÂ dashsizeÂ ä»æé«å°æä½ç
      const gss = [4, 1, 3, 0, 2]; //Â çº¿åé´éå¤§å°Â ä»æé«å°æä½ç
      const py = [-20, -20, -20, -30, -30]; //Â çº¿åå¨Â yÂ è½´ä½ç½®Â ä»æé«å°æä½ç
      const group = new THREE.Group();

      for (let i = 0; i < rs.length; i++) {
        const line = this.createCircleDashLine(
          rs[i],
          this.GREEN_LINE_WIDTH,
          dss[i],
          gss[i],
          this.GREEN_LINE_COLOR
        );

        line.position.set(0, py[i], 0);

        group.add(line);
      }

      this.scene.add(group);
    } /**

Â Â Â Â Â *Â @description:åå»ºåçº¿

Â Â Â Â Â *Â @paramÂ {number}Â çº¿ååå¾

Â Â Â Â Â *Â @paramÂ {number}Â çº¿å®½

Â Â Â Â Â *Â @paramÂ {number}Â çº¿çé¿åº¦

Â Â Â Â Â *Â @paramÂ {number}Â é´éé¿åº¦

Â Â Â Â Â *Â @return:

Â Â Â Â Â */,

    createCircleDashLine(
      r = 10,
      w = this.GREEN_LINE_WIDTH,
      ds = 1,
      gs = 1,
      color = this.GREEN_LINE_COLOR
    ) {
      const points = this.getPosition(r, 100, 0);

      const vertexes = points.reduce((acc, item) => {
        return acc.concat([item[0], 0, item[1]]);
      }, []);

      const geometry = new THREE.BufferGeometry();

      geometry.setAttribute(
        "position",
        new THREE.Float32BufferAttribute(vertexes, 3)
      );

      const material = new THREE.LineDashedMaterial({
        color,

        linewidth: w,

        dashSize: ds,

        gapSize: gs
      });

      const line = new THREE.Line(geometry, material);

      line.computeLineDistances();

      return line;
    } /**

Â Â Â Â Â *Â @description:

Â Â Â Â Â *Â @paramÂ {Number}Â AÂ Â Â åå¸åå¾

Â Â Â Â Â *Â @paramÂ {Number}Â numÂ åå¸çç©ä½ä¸ªæ°

Â Â Â Â Â *Â @paramÂ {Number}Â ÏÂ Â Â åå§è§åº¦

Â Â Â Â Â *Â @return:Â Array

Â Â Â Â Â */, //Â æ ¹æ®æ­£å¼¦Â ä½å¼¦Â Â x=Asin(Ït+Ï)+kÂ z=Acos(Ït+Ï)+k

    getPosition(A, num, Î¨ = 0) {
      const position = [];

      const w = (Math.PI * 2) / num;

      for (let t = 0; t < num; t++) {
        const Î¸ = w * t + Î¨;

        position.push([A * Math.cos(Î¸), A * Math.sin(Î¸), Î¸]);
      }

      return position;
    },
    /**

Â Â Â Â Â *Â @description:Â æ ¹æ®å¼§å½¢çæ®µçå®½åº¦ãé´éãä¸ªæ°æ±åºå¼§å½¢çæ®µæå´æçåçåå¾

Â Â Â Â Â *Â @paramÂ {Number}Â wÂ å¼§å½¢çæ®µçå®½åº¦

Â Â Â Â Â *Â @paramÂ {Number}Â gapÂ å¼§å½¢çæ®µçé´é

Â Â Â Â Â *Â @paramÂ {Number}Â nÂ å¼§å½¢çæ®µçä¸ªæ°

Â Â Â Â Â *Â @return:

Â Â Â Â Â */

    getR(w, gap, n) {
      const side = w + gap;
      const Î¸ = (2 * Math.PI) / n;
      return side / (2 * Math.sin(Î¸ / 2));
    }
  }
};
</script>

